'use client'

import { use, useState, useEffect } from 'react'
import { useCart } from '@/contexts/CartContext'
import Image from 'next/image'
import { useRouter } from 'next/navigation'
import { ShoppingCart, CreditCard } from 'lucide-react'

interface Product {
  id: string
  name: string
  price: number
  description: string
  images: string[]
  category: string
  stock: number
  brand: string
}

interface PageProps {
  params: Promise<{ id: string }>
}

export default function ProductPage({ params }: PageProps) {
  const resolvedParams = use(params)
  const productId = resolvedParams.id

  const [product, setProduct] = useState<Product | null>(null)
  const [quantity, setQuantity] = useState(1)
  const [loading, setLoading] = useState(true)
  const [selectedImage, setSelectedImage] = useState(0)
  const { addToCart } = useCart()
  const router = useRouter()

  useEffect(() => {
    if (productId) {
      fetchProduct()
    }
  }, [productId])

  const fetchProduct = async () => {
    try {
      setLoading(true)
      const response = await fetch(`http://localhost:8000/api/products/${productId}`)
      if (response.ok) {
        const data = await response.json()
        setProduct(data)
      } else {
        console.error('Product not found')
      }
    } catch (error) {
      console.error('Error fetching product:', error)
    } finally {
      setLoading(false)
    }
  }

  const handleBuyNow = () => {
    if (!product) return

    const buyNowData = {
      items: [{
        product_id: product.id,
        product_name: product.name,
        price: product.price,
        quantity: quantity,
        image: product.images?.[0] || '/images/products/placeholder.jpg'
      }],
      total: product.price * quantity,
      buyNow: true
    }
    sessionStorage.setItem('buyNowData', JSON.stringify(buyNowData))
    router.push('/checkout')
  }

  const handleAddToCart = async () => {
    if (!product) return
    await addToCart(product, quantity)
  }

  if (loading) {
    return (
      <div className="min-h-screen bg-white flex items-center justify-center">
        <p className="text-xl">Loading...</p>
      </div>
    )
  }

  if (!product) {
    return (
      <div className="min-h-screen bg-white flex items-center justify-center">
        <div className="text-center">
          <p className="text-xl mb-4">Product not found</p>
          <button
            onClick={() => router.push('/products')}
            className="bg-black text-white px-6 py-2 rounded"
          >
            Back to Products
          </button>
        </div>
      </div>
    )
  }

  const imageUrl = product.images && product.images.length > 0
    ? product.images[selectedImage]
    : '/images/products/placeholder.jpg'

  return (
    <div className="min-h-screen bg-white py-12">
      <div className="container-custom">
        <div className="grid md:grid-cols-2 gap-8 lg:gap-12">
          {/* Product Images */}
          <div>
            <div className="relative aspect-square bg-gray-100 rounded-lg overflow-hidden">
              <Image
                src={imageUrl}
                alt={product.name}
                fill
                className="object-cover"
                priority
              />
            </div>

            {/* Thumbnail images */}
            {product.images && product.images.length > 1 && (
              <div className="grid grid-cols-4 gap-2 mt-4">
                {product.images.slice(0, 4).map((img, idx) => (
                  <button
                    key={idx}
                    onClick={() => setSelectedImage(idx)}
                    className={`relative aspect-square bg-gray-100 rounded overflow-hidden border-2 ${
                      selectedImage === idx ? 'border-black' : 'border-transparent'
                    }`}
                  >
                    <Image
                      src={img}
                      alt={`${product.name} ${idx + 1}`}
                      fill
                      className="object-cover"
                    />
                  </button>
                ))}
              </div>
            )}
          </div>

          {/* Product Info */}
          <div>
            {/* Category & Brand */}
            <div className="flex items-center gap-4 text-sm text-gray-600 mb-2">
              <span className="uppercase">{product.category}</span>
              <span>•</span>
              <span>{product.brand}</span>
            </div>

            <h1 className="text-3xl md:text-4xl font-light mb-4">{product.name}</h1>

            <p className="text-2xl md:text-3xl font-light mb-6">৳{product.price.toFixed(2)} BDT</p>

            <p className="text-gray-600 mb-8 leading-relaxed">{product.description}</p>

            {/* Stock Status */}
            <div className="mb-6">
              {product.stock > 0 ? (
                <p className="text-green-600">✓ In Stock ({product.stock} available)</p>
              ) : (
                <p className="text-red-600">Out of Stock</p>
              )}
            </div>

            {/* Quantity Selector */}
            <div className="mb-8">
              <label className="block text-sm font-medium mb-2">Quantity</label>
              <div className="flex items-center gap-2">
                <button
                  onClick={() => setQuantity(Math.max(1, quantity - 1))}
                  className="px-3 py-1 border border-gray-300 rounded hover:bg-gray-50 disabled:opacity-50"
                  disabled={quantity <= 1}
                >
                  -
                </button>
                <input
                  type="number"
                  min="1"
                  max={product.stock}
                  value={quantity}
                  onChange={(e) => {
                    const val = parseInt(e.target.value) || 1
                    setQuantity(Math.min(product.stock, Math.max(1, val)))
                  }}
                  className="w-20 px-3 py-1 border border-gray-300 rounded text-center"
                />
                <button
                  onClick={() => setQuantity(Math.min(product.stock, quantity + 1))}
                  className="px-3 py-1 border border-gray-300 rounded hover:bg-gray-50 disabled:opacity-50"
                  disabled={quantity >= product.stock}
                >
                  +
                </button>
              </div>
            </div>

            {/* Action Buttons */}
            <div className="flex flex-col sm:flex-row gap-4">
              <button
                onClick={handleAddToCart}
                className="flex-1 flex items-center justify-center gap-2 bg-white text-black border-2 border-black px-6 py-3 rounded-lg hover:bg-gray-100 transition-colors disabled:opacity-50"
                disabled={product.stock === 0}
              >
                <ShoppingCart className="w-5 h-5" />
                Add to Cart
              </button>

              <button
                onClick={handleBuyNow}
                className="flex-1 flex items-center justify-center gap-2 bg-black text-white px-6 py-3 rounded-lg hover:bg-gray-800 transition-colors disabled:opacity-50"
                disabled={product.stock === 0}
              >
                <CreditCard className="w-5 h-5" />
                Buy Now
              </button>
            </div>

            {/* Additional Info */}
            <div className="mt-12 pt-8 border-t">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <div>
                  <h3 className="font-medium mb-2">Free Shipping</h3>
                  <p className="text-sm text-gray-600">On orders over ৳5,000</p>
                </div>
                <div>
                  <h3 className="font-medium mb-2">2 Years Warranty</h3>
                  <p className="text-sm text-gray-600">Manufacturer warranty included</p>
                </div>
                <div>
                  <h3 className="font-medium mb-2">COD Available</h3>
                  <p className="text-sm text-gray-600">Cash on delivery option</p>
                </div>
                <div>
                  <h3 className="font-medium mb-2">Easy Returns</h3>
                  <p className="text-sm text-gray-600">7 days return policy</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}